html, body{
    padding:0;
    margin:0;
    font-family: 'Open Sans', sans-serif;
}
* {
    box-sizing: border-box
}

section {
    display: grid;
    grid-template-columns: auto 10%;
    height: 100%;
}

aside {
    background-color: #f4f4f4;
    margin:0;
    text-align: center;
    box-shadow: -4px 1px 5px 0px #d5d5d5;
}

article {
    padding: 25em 15em;
    display: flex;
    align-content: center;
    flex-direction: column;
    justify-content: center;
}

p {
    line-height: 1.5;
    font-size: 25px;
}

h1 {
    font-size: 30px;
}

img {
    width: 80px;
    position: sticky;
    position: -webkit-sticky;
    top: calc(50% - 40px);
}